<template>
	<div class="modal fade" id="categoryModal" tabindex="-1" role="dialog" aria-labelledby="categoryModalTitle" aria-hidden="true">
		<div class="modal-dialog" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="categoryModalTitle">Add Badge</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body modal__body">
					<form 
						@submit.prevent="createBadge"
						class="modal__form" 
						method="post">
						<div class="form-group">
							<label class="modal__label" for="badge">Name</label>
							<input 
								type="text" 
								:class="[{'is-invalid': errors.name}]"
								class="form-control" 
								id="badge" 
								placeholder="Badge Name"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.name">
							<div class="invalid-feedback">
								{{ errors.name }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="badge">Value</label>
							<input 
								type="number" 
								:class="[{'is-invalid': errors.value}]"
								class="form-control" 
								id="badge" 
								placeholder="Badge Value"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.value">
							<div class="invalid-feedback">
								{{ errors.value }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="action">Select an action</label>
							<select 
								class="custom-select form-control"  
								id="category"
								:class="[{'is-invalid': errors.action}]"
								v-model="itemData.action">
								<option value="exams">Exams</option>
								<option value="forum">Forum</option>
							</select>
							<div class="invalid-feedback">
								{{ errors.action }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="action">Select a criteria</label>
							<select 
								class="custom-select form-control"  
								id="category"
								:class="[{'is-invalid': errors.criteria}]"
								v-model="itemData.criteria">
								<option value=">">Gretaer Than</option>
								<option value="<">Less Than</option>
								<option value="=">Equal To</option>
							</select>
							<div class="invalid-feedback">
								{{ errors.criteria }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="avatar-file">
								Badge Image
							</label>
							<input
								ref="avatar"
								class="avatar__input"
								id="avatar-file"
								type="file"
								@change="loadImage"
								accept="image/*">
							<div class="invalid-feedback">
								{{ errors.file }}
							</div>
						</div>
					</form>
				</div>
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button 
						@click="clearErrors()"
						class="btn btn--md" 
						type="button"
						data-dismiss="modal"
						aria-label="close"
						name="button">Cancel</button>
					<button 
						@click.prevent="createBadge"
						class="btn btn--md btn__primary" 
						type="submit" 
						name="button">
						Save Badge</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import form from '../../mixins/form';
import Loader from '../../components/Loader';
import { createBadge } from '../../utils/api/badge';
import badge from '../../utils/validations/badge';
import { success, fail } from '../../utils/toast';
import eventHub from '../../utils/eventhub';

export default {
	name: 'AddBadge',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loading: false
		};
	},
	components: {
		Loader
	},
	watch: {
		issues(val) {
			this.errors = { ...val };
		}
	},
	methods: {
		createBadge() {
			this.validate(this.itemData, badge.create);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			createBadge(this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.itemData = {};
						this.loading = false;
						eventHub.$emit('badge-created', response.data);
						success({
							title: 'Success! A new badge has been created'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong'
					});
					return error;
				});
			return true;
		},
		loadImage() {
			const img = this.$refs.avatar.files[0];
			const imageSize = 3 * (2 ** 20);
			if (img && img.type.match(/image\/*/)) {
				if (img.size > imageSize) {
					errors.file = 'File size greater than 3MB';
					return false;
				}
			}
			const reader = new FileReader();
			reader.onload = (e) => {
				this.itemData.file = e.target.result;
			};
			reader.readAsDataURL(img);
			return true;
		}
	}
};
</script>
